div.resume-wrapper {
    display: flex;
    //background-color: rgb(255, 248, 232);
    font-size: 1.1em;
    height: 100%;
    width: 100%;

    @media (min-width: 1166px) {
        flex-direction: row;

        > :not(:last-child) {
            margin-right: 1em;
        }

        div.general {
            width: 100%;
        }
    }

    @media (max-width: 1165px) {
        width: 100%;
        flex-direction: column;
        flex-wrap: wrap;
    }

    .section-header {
        letter-spacing: 0.1em;
        
        @media (min-width: 1166px) {
            margin-top: 0.5em;
            margin-bottom: 0.7em;
        }

        @media (max-width: 1165px) {
            margin: 1em 0;
        }
    }

    .general {
        height: 100%;
        overflow: auto;

        div.skills-block {
            border-left: 3px dotted rgb(0, 95, 133);
            background-color: rgb(238, 255, 255);

            .section-header {
                color: rgb(0, 86, 126);
            }

            div.inner-block {
                flex-direction: row;

                .sub-header {
                    color: rgb(0, 152, 190);
                }

                .desc-text {
                    color: rgb(0, 86, 126);
                }
            }
        }

        div.exp-block {
            background-color: rgb(237, 255, 235);
            border-left: 3px dotted rgb(4, 133, 0);

            .section-header {
                color: rgb(0, 126, 27);
            }

            .sub-header {
                color: rgb(0, 190, 85);
            }

            .desc-text {
                color: rgb(0, 126, 27);
            }

            li:before {
                color: rgb(0, 190, 85);
            }
        }
        
        div.awards-block {
            background-color: rgb(255, 247, 249);
            border-left: 3px dotted rgb(133, 0, 51);

            .section-header {
                color: rgb(126, 0, 48);
            }

            .sub-header {
                color: rgb(190, 0, 57);
            }

            .desc-text {
                color: rgb(126, 0, 38);
            }

            li:before {
                color: rgb(190, 0, 63);
            }
        }

        div.edu-block {
            background-color: rgb(255, 252, 247);
            border-left: 3px dotted rgb(133, 75, 0);

            .section-header {
                color: rgb(126, 57, 0);
            }

            .sub-header {
                color: rgb(190, 85, 0);
            }

            .desc-text {
                color: rgb(126, 71, 0);
            }

            li:before {
                color: rgb(190, 92, 0);
            }
        }

        div.detail-block {
            div.inner-block {
                flex-direction: column;

                &:not(:nth-child(2)) {
                    padding-top: 0.3em;
                }

                div.sub {           
                    display: flex;

                    @media (min-width: 1166px) {
                        align-items: center;
                        justify-content: space-between;
                    }

                    @media (max-width: 1165px) {
                        flex-direction: column;
                        justify-content: center;

                        .sub-header {
                            display: flex;
                            flex-direction: column;
                            align-items: center;

                            .subsub-header {
                                margin-top: 0.5em;
                            }
                        }

                        .date-header {
                            margin: 0;
                            font-size: 0.8em;
                            font-style: italic;
                        }
                    }

                    .sub-header {
                        .subsub-header, &.date-header {
                            font-weight: lighter;
                            
                            @media (max-width: 1165px) {
                                text-align: center;
                            }
                        }

                        @media (min-width: 1166px) {
                            &.date-header {
                                font-size: 0.9em;
                            }
                        }
                    }
                }

                div.desc {
                    @media (max-width: 1165px) {
                        margin: 0.3em 0;
                    }

                    .desc-text {
                        @media (min-width: 1166px) {
                            padding-left: 2em;
                        }

                        @media (max-width: 1165px) {
                            padding-left: 11px;
                        }

                        li {
                            list-style: none;
                            margin-top: 0.5em;
                        }

                        li:before {
                            content: '\2022';
                            display: block;
                            position: relative;
                            max-width: 0;
                            max-height: 0;
                            left: -11px;
                            top: 0;
                            font-size: 12px;
                        }
                    }
                }
            }
        }

        div.info-block {
            padding: 0.5em 1.5em 0.7em 1.5em;
            
            &:not(:last-child) {
                margin-bottom: 0.5em;
            }

            div.inner-block {
                display: flex;
                flex-wrap: wrap;
                margin: 0.3em 0;

                @media (max-width: 1165px) {
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    .sub-header {
                        margin: 0.5em 0;
                    }
                }

                .sub-header, .desc-text {
                    line-height: 16px;
                }

                @media (min-width: 1166px) {
                    .sub-header {
                        margin: 0;
                        margin-right: 0.5em;
                    }
                }

                .desc-text {
                    margin: 0;
                    font-weight: lighter;
                    font-size: 0.7em;
                }

                div.list-block {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                }
            }
        }
    }

    div.events-wrapper {
        flex-direction: column;
        flex-wrap: nowrap;
        flex: 0 0 30%;
        height: 100%;
        width: 100%;

        @media (max-width: 1165px) {
            display: none;
        }

        > h3.section-header {
            color: rgb(0, 218, 218);
            font-size: 0.8em;
            margin: 0;
            margin-bottom: 1em;
        }
    }

    div.timeline-wrapper {
        height: calc(100% - 5em);
        width: 100%;
        position: relative;
        flex-direction: row;
        
        * {
            font-weight: lighter;
        }

        div.timeline {
            color: rgb(121, 121, 121);
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: start;
            flex-wrap: nowrap;
            overflow-y: scroll;
    
            div.event {
                z-index: 2;
                background-color: white;
                padding: 0.5em 0;
                margin: 1em 0;
                text-align: center;
    
                h4, h6 {
                    margin: 0;
                }
            }
        }
    
        div.timeline-line {
            overflow: show;
            width: 50%;
            height: 100%;
            float: left;
            position: absolute;
            border-right: 1px dotted rgb(0, 218, 218);
        }
    }
}